.c-navbar {
  width: 100%;
  background-color: $color-blauw;

  &__container {
    display: flex;
    justify-content: flex-end;
    position: relative;

    @include media-breakpoint-down(sm) {
      position: static;
    }
  }

  &__logo {
    background-color: #fff;
    width: 17rem;
    position: absolute;
    left: 0;
    top: 0;
    box-shadow: 0 3px 6px #888888;
    z-index: 1000;

    @include media-breakpoint-down(sm) {
      left: 2.6rem;
    }

    @include media-breakpoint-down(xs) {
      width: 11rem;
    }

    img {
      padding: 1rem 2rem 2rem;
      width: 100%;
    }
  }

  &__items {
    display: flex;
    align-items: center;
    padding: 2rem;

    @include media-breakpoint-down(sm) {
      display: none;
      width: 100vw;
      height: 100%;
      padding-top: 5rem;
      position: absolute;
      left: 0;
      background-color: #354b60;
      margin: 8rem 0;
      flex-direction: column;
    }
  }
  &__item {
    list-style: none;

    @include media-breakpoint-down(sm) {
      margin: 4rem 0 0rem;
    }

    a {
      color: $color-white;
      text-decoration: none;
      font-size: 1.8rem;
      padding: 1.5rem 1.5rem;
      margin: 0 1rem;
      position: relative;

      &::after {
        content: "";
        width: 100%;
        background-color: transparent;
        height: 0.5rem;
        position: absolute;
        bottom: 0;
        left: 0;
        border-radius: 15px;
      }
      &:hover {
        &::after {
          background-color: $color-secondary;
        }
      }
    }
  }

  //hamburger menu
  .hammenu {
    width: 3rem;
    display: none;
    cursor: pointer;
    padding: 3rem 0;

    @include media-breakpoint-down(sm) {
      display: inline-block;
    }

    &__bar {
      width: 100%;
      height: 4px;
      border-radius: 4px;
      display: block;
      background: $color-white;
      margin-bottom: 4px;
      transition: all 0.3s;
      transform-origin: 26px 2px;
    } //end bar
  } //end hammenu

  .js-transformed {
    .hammenu__bar--1 {
      transform: rotate(-45deg);
    }
    .hammenu__bar--2 {
      opacity: 0;
    }
    .hammenu__bar--3 {
      transform: rotate(45deg);
    }
  }
}
